
import Vue from 'vue'
import { useServicesStore } from '@/store/services';
const servicesStore = useServicesStore();

export default Vue.extend({
  props: {
    id: {
      type: String
    },
  },
  data: () => ({
    price: 0
  }),

  methods: {
    send() {
      servicesStore.priceOfferService({
        id: this.id,
        price_offer: this.price
      }).then(res => {
        this.price = 0
        this.$vToastify.success(res);
      })
    }
  }
})
